<template>
  <div class="container">
    <page-title first-title="账户信息"></page-title>

    <suppliers-base-info :suppliersInfo="suppliersInfo" :suppliersId="suppliersId"></suppliers-base-info>

    <div class="search-wrapper">
      <el-form class="demo-form-inline" :inline="true" :model="searchParams">
        <el-form-item label="汇总结算单编号">
          <el-input class="search-input" v-model="searchParams.multiStatementNo"></el-input>
        </el-form-item>
        <el-form-item label="生成日期">
          <el-date-picker
            class="search-input"
            v-model="addtime"
            type="date"
            placeholder="请选择开始日期"
          ></el-date-picker>
          -
          <el-date-picker
            class="search-input"
            v-model="endtime"
            type="date"
            placeholder="请选择截止日期"
          ></el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">搜索</el-button>
          <el-button @click="clear">清空</el-button>
        </el-form-item>
      </el-form>
    </div>

    <el-table :data="multiStatementList" style="width: 100%" :header-cell-style="{ background: '#eef1f6' }">
      <el-table-column prop="multi_statement_no" label="汇总结算单编号"></el-table-column>
      <el-table-column prop="addtime" :formatter="dateFormat" label="创建时间"></el-table-column>
      <!-- <el-table-column prop="all_income" label="总收入（元）"></el-table-column>
      <el-table-column prop="all_expend" label="总支出（元）"></el-table-column> -->
      <!-- <el-table-column prop="all_cash_income" label="现金总收入（元）"></el-table-column> -->
      <!-- <el-table-column prop="all_huanbi_income" label="积分总收入（元）"></el-table-column> -->
      <el-table-column prop="has_one_suppliers.suppliers_name" label="商户"></el-table-column>
      <el-table-column prop="suppliers_account" label="收款账号"></el-table-column>
      <el-table-column prop="pay_name" label="支付方式"></el-table-column>
      <el-table-column prop="payee" label="收款人"></el-table-column>
      <el-table-column prop="real_pay" label="实付金额"></el-table-column>
      <el-table-column prop="service_fee" label="手续费"></el-table-column>
      <el-table-column prop="status" label="状态"></el-table-column>
      <el-table-column prop="last_modified" :formatter="dateFormat" label="最后操作时间"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="prop">
          <el-button
            type="text"
            size="samll"
            @click="navToMultiStatementForm(prop.row.multi_statement_no, prop.row.suppliers_id)"
            >查看</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
      background
      @current-change="currentChange"
      :total="total"
      :currentPage="currentPage"
      :page-size="searchParams.perPage"
      layout="prev, pager, next, jumper"
    ></el-pagination>
  </div>
</template>

<script>
import SuppliersBaseInfo from '../../components/suppliers/SuppliersBaseInfo'
import { getSuppliersInfo, getSuppliersStatementList, getSuppliersMultiStatementList } from '../../api/suppliers'
import { mapState } from 'vuex'
import { PAY_METHODS, MULTI_STATEMENT_STATUS } from '../../const/status'

export default {
  name: 'MultiSuppliersStatementList',
  components: {
    SuppliersBaseInfo
  },
  data() {
    return {
      addtime: '',
      endtime: '',
      suppliersInfo: {
        suppliers_name: '',
        shop_name: '',
        manager_name: '',
        manager_tel: ''
      },
      searchParams: {
        suppliersId: '',
        perPage: 10,
        page: 1,
        addtime: 0, //添加时间
        endtime: 0,
        multiStatementNo: '' //结算单号
      },
      multiStatementList: [],

      total: 0,
      currentPage: 1,
      perPage: 0
    }
  },
  computed: {
    ...mapState({
      suppliersId: state => state.suppliers.suppliers_id
    })
  },
  created() {
    this.getSuppliersInfo()
    // this.getStatementList()
    this.getMultiStatementList()
  },
  methods: {
    // 获取供应商信息
    getSuppliersInfo() {
      getSuppliersInfo(this.suppliersId).then(res => {
        this.suppliersInfo = res
      })
    },

    // 获取结算单列表
    getStatementList() {
      this.searchParams.suppliers_id = this.suppliersId
      this.searchParams.addtime = this.addtime / 1000
      this.searchParams.endtime = this.endtime / 1000
      getSuppliersStatementList(this.searchParams).then(res => {
        this.statementList = res.list
        this.paged = res.paged
        console.log(this.statementList)
      })
    },

    // 获取汇总单列表
    getMultiStatementList() {
      this.searchParams.suppliersId = this.suppliersId
      this.searchParams.addtime = this.addtime / 1000
      this.searchParams.endtime = this.endtime / 1000
      getSuppliersMultiStatementList(this.searchParams).then(res => {
        this.multiStatementList = res.list
        this.total = res.paged.total
        this.currentPage = res.paged.currentPage
        this.perPage = res.paged.perPage

        for (var i in this.multiStatementList) {
          this.multiStatementList[i].status = MULTI_STATEMENT_STATUS[this.multiStatementList[i].status]
          this.multiStatementList[i].pay_name = PAY_METHODS[this.multiStatementList[i].pay_name]
        }
      })
    },

    dateFormat: function(row, column) {
      let date = row[column.property]
      return this.utils.formatDate('YYYY-MM-DD HH:mm:ss', date * 1e3)
    },

    // 按条件检索汇总结算单列表
    search() {
      this.searchParams.page = 1
      this.getMultiStatementList()
    },

    currentChange(page) {
      this.searchParams.page = page
      this.getMultiStatementList()
    },

    clear() {
      this.searchParams.multiStatementNo = ''
      this.searchParams.page = 1
      this.addtime = 0
      this.endtime = 0
      this.getMultiStatementList()
    },

    navToMultiStatementForm(multiStatementNo, suppliersId) {
      this.$router.push({
        name: 'MultiSuppliersStatementForm',
        params: {
          multiStatementNo: multiStatementNo,
          suppliersId: suppliersId,
          type: 'show'
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  font-size: 15px;

  .search-wrapper {
    padding: 10px;
    margin: 10px 0;
    background-color: #fff;
    border: 1px solid #eee;
    .el-form-item {
      margin-bottom: 0;
      .search-input {
        width: 180px;
      }
    }
  }

  .el-pagination {
    text-align: center;
    margin-top: 10px;
  }
}
</style>
