import { fetchEndpoint, fetchEndpointBlob } from '../server/network'

// 商家资产列表（仅管理员查看）
export const getSuppliersList = params =>
  fetchEndpoint('/admin/api.suppliers.list', 'POST', {
    suppliers_name: params.suppliers_name,
    shop_name: params.shop_name,
    page: params.page,
    size: params.size
  })

// 账户总览
export const getSuppliersInfo = id =>
  fetchEndpoint('/admin/api.suppliers.info', 'POST', {
    suppliers_id: id //商家id
  })

// 账户资金信息（积分、信用额度等）
export const getSuppliersAccount = suppliers_id =>
  fetchEndpoint('/admin/api.suppliers.account.info', 'POST', {
    suppliers_id: suppliers_id
  })

// 对账单列表
export const getSuppliersStatementList = params =>
  fetchEndpoint('/admin/api.suppliers.statement.list', 'POST', {
    type: params.type, //账单类型 1：日账单，2:月账单
    suppliers_id: params.suppliers_id, //商家id
    size: params.size, //单页条数 10
    page: params.page, //页码
    addtime: params.addtime, //添加时间
    endtime: params.endtime, //结束时间
    statement_no: params.statement_no, // 结算单号
    status: params.status
  })

// 对账单详情
export const getSuppliersStatementInfo = params =>
  fetchEndpoint('/admin/api.suppliers.statement.info', 'POST', {
    suppliers_statement_id: params.suppliers_statement_id, //账单id
    suppliers_id: params.suppliers_id //商家id
  })

// 对账单详情--收入/支出列表
export const getSuppliersStatementDetailList = params =>
  fetchEndpoint('/admin/api.suppliers.statement.detail.list', 'POST', {
    type: params.type, //1收入，2支出
    suppliers_id: params.suppliers_id, //  商家id
    suppliers_statement_id: params.suppliers_statement_id, //结算单id
    page: params.page,
    size: params.size
  })

export const exportList = params =>
  fetchEndpointBlob('/admin/api.suppliers.statement.statementExport', 'POST', {
    suppliers_id: params.suppliers_id,
    suppliers_statement_id: params.suppliers_statement_id
  })

// 此接口已废弃
export const getSuppliersMultiStatementList = params =>
  fetchEndpoint('/admin/api.suppliers.statement.getMultiStatementLIst', 'POST', {
    suppliers_id: params.suppliersId,
    multi_statement_no: params.multiStatementNo,
    addtime: params.addtime,
    endtime: params.endtime,
    page: params.page,
    size: params.perPage
  })

// 此接口已废弃
export const getSuppliersMultiStatementInfo = params =>
  fetchEndpoint('/admin/api.suppliers.statement.getMultiStatementInfo', 'POST', {
    suppliers_id: params.suppliersId,
    multi_statement_no: params.multiStatementNo
  })

// 此接口已废弃
export const createMultiStatement = params =>
  fetchEndpoint('/admin/api.suppliers.statement.createMultiStatement', 'POST', {
    suppliers_id: params.suppliersId,
    suppliers_statement_id: params.statementIds,
    suppliers_account: params.suppliersAccount,
    pay_name: params.payName,
    payee: params.payee,
    real_pay: params.realPay,
    service_fee: params.serviceFee,
    settlement_log: params.settlementLog
  })

// 此接口已废弃
export const confirmMultiStatement = params =>
  fetchEndpoint('/admin/api.suppliers.statement.confirmMultiStatement', 'POST', {
    suppliers_id: params.suppliersId,
    multi_statement_no: params.multiStatementNo,
    status: params.status,
    settlement_log: params.settlementLog
  })

// 此接口已废弃
export const getLastMultiStatementInfo = params =>
  fetchEndpoint('/admin/api.suppliers.statement.getLastMultiStatementInfo', 'POST', {
    suppliers_id: params.suppliersId
  })

/**
 * @DateTime 2019-07-15
 * @description  [账户汇款信息设置]
 * @param    {[int]} suppliers_id [供应商ID]
 * @param    {[array[int]]} suppliers_statement_id [结算单号]
 * @return   {[type]} [description]
 */
export const statementPreview = (suppliers_id, suppliers_statement_id) =>
  fetchEndpoint('/admin/api.multi.statement.preview', 'POST', {
    suppliers_id: suppliers_id,
    suppliers_statement_id: suppliers_statement_id
  })

/**
 * @DateTime 2019-07-18
 * @description  [商家提现积分]
 * @param    {[int]} suppliers_id [商家提现积分最大20000，最小0]
 */
export const huanbiWithdraw = (suppliers_id, huanbi_remit) =>
  fetchEndpoint('/admin/api.suppliers.huanbi.withdraw', 'POST', {
    suppliers_id: suppliers_id,
    huanbi_remit: huanbi_remit
  })

/**
 * @DateTime 2019-07-18
 * @description  [商家提现积分流水]
 */
export const withdrawList = (suppliers_id, page, size = 10) =>
  fetchEndpoint('/admin/api.suppliers.huanbi.withdraw.list', 'POST', {
    suppliers_id: suppliers_id,
    page: page,
    size: size
  })

/**
 * @DateTime 20190912
 * @description [积分转账]
 */
export const getTransferDetail = (page, size = 10) =>
  fetchEndpoint('/admin/api.suppliers.transfer.details', 'POST', {
    page: page,
    size: size
  })

export const transferHB = (to, from, huanbi) =>
  fetchEndpoint('/admin/api.suppliers.transfer.surplus', 'POST', {
    to: to,
    from: from,
    huanbi: huanbi
  })

export const getTransferDetailList = (suppliers_id, page, size = 10) =>
  fetchEndpoint('/admin/api.suppliers.transfer.list', 'POST', {
    suppliers_id: suppliers_id,
    page: page,
    size: size
  })

export const getSuppliersAll = suppliers_name =>
  fetchEndpoint('/admin/api.from.suppliers.get', 'POST', {
    suppliers_name: suppliers_name
  })
