<template>
  <div id="app">
    <router-view v-if="isRouterAlive" />
    <page-footer />
  </div>
</template>

<script>
import PageFooter from './components/common/PageFooter'
export default {
  name: 'app',
  // 提供reload方法
  provide: function () {
    return {
      reload: this.reload
    }
  },
  data() {
    return {
      isRouterAlive: true,
    }
  },
  components: {
    'page-footer': PageFooter
  },
  methods: {
    reload() {
      this.isRouterAlive = false
      this.$nextTick(function() {
        this.isRouterActive = true;
        this.isRouterAlive = true;
      })
    }
  }
}
</script>

