import Vue from 'vue'
import App from './App.vue'

import Accounting from 'accounting' // 金钱相关扩展
import ElementUI from 'element-ui'
import PageTitle from './components/common/PageTitle'

import router from './router'
import store from './store'
import utils from './util/util'

import VueClipboard from 'vue-clipboard2' // 复制插件
import vcolorpicker from 'vcolorpicker' // 颜色选择

import * as data from './data' // 开发环境才会引入

// import './assets/style/reset.scss'
import 'element-ui/lib/theme-chalk/index.css'

import axios from 'axios'

Vue.config.productionTip = false

// 将时间戳转成日期
Vue.filter('convertTime', function(timeStr) {
  return utils.formatDate('YYYY-MM-DD HH:mm:ss', timeStr)
})
// 金额格式化
Vue.filter('formatMoney', function(money) {
  return utils.formatMoney(money)
})

let name = window.vue_path // PHP渲染的VUE路由名标记

router.beforeEach((to, from, next) => {
  // sentry
  const $sentry = Vue.prototype.$sentry
  $sentry &&
    $sentry.configureScope(scope => {
      scope.setTag('url', to.fullPath)
      if (to.meta.title) {
        scope.setTag('title', to.meta.title)
      }
    })

  if (process.env.NODE_ENV === 'development') {
    // 开发环境一率走这获取数据
    name = to['name']

    Vue.prototype.$Data = data[name]
  } else if (from['name'] && to['name'] == 'home') {
    // 返回到上一页
    history.go(-1)
    return next(false)
  } else if (!from['name'] && to['name'] != name) {
    // 首次浏览就取页面数据
    Vue.prototype.$Data = JSON.parse(document.querySelector('#vue_data').textContent)
    return next({ name: name })
  }
  next()
})

Vue.use(ElementUI)
Vue.use(VueClipboard)
Vue.use(vcolorpicker)

Vue.prototype.utils = utils
Vue.prototype.$accounting = Accounting

import api from './api/workorder.js'
// 将API方法绑定到全局
Vue.prototype.$api = api
Vue.prototype.axios = axios

// sentry
// 文档: https://github.com/791045873/notes/issues/15
// todo: sourcemap => https://juejin.im/post/5cc2b8b9e51d456e40377319
//       sentry-cli releases -o report -p hhyw-h5admin new pro@1.0.4
//       sentry-cli releases -o report -p hhyw-h5admin files pro@1.0.4 upload-sourcemaps ../ecshop/admin/templates_vue/static/js --url-prefix ~/admin/templates_vue/static/js
//       rm ../ecshop/admin/templates_vue/static/js/*.js.map
//
//       把 sourcemap 上传后删除 map: https://github.com/40thieves/webpack-sentry-plugin
import * as Sentry from '@sentry/browser'
import * as Integrations from '@sentry/integrations'
if (process.env.VUE_APP_SENTRY_DSN) {
  Sentry.init({
    environment: process.env.NODE_ENV,
    dsn: process.env.VUE_APP_SENTRY_DSN,
    release: 'pro@1.0.4',
    integrations: [new Integrations.Vue({ Vue, attachProps: true })]
  })
  if (window.admin_id) {
    Sentry.configureScope(scope => {
      scope.setUser({ id: window.admin_id })
    })
  }
  Vue.prototype.$sentry = Sentry
}

// 全局注册组件
Vue.component('page-title', PageTitle)

new Vue({
  router,
  store,
  render: h => h(App),
  methods: {
    reload() {
      // 刷新路由
      // 调用方式: this.$root.reload()
      this.$children[0].isRouterAlive = false
      this.$nextTick(() => (this.$children[0].isRouterAlive = true))
    }
  }
}).$mount('#app')

export { router, store }
