const state = {
  suppliers_id: 0,
  suppliers_name: '',
  preview: {},
  stateIdList: [],
  cooperateType: 1
}

// mutations
const mutations = {
  setSuppliersId(state, item) {
    state.suppliers_id = item
  },
  setSupplierName(state, item) {
    state.suppliers_name = item
  },
  saveStatementPreview(state, payload) {
    state.preview = payload
  },
  saveStatementIdList(state, payload) {
    state.stateIdList = payload
  },
  saveCooperateType(state, item) {
    state.cooperateType = item
  }
}

export default {
  state,
  mutations
}
