import ROOT from './root.js'
// 引用axios
var axios = require('axios')
import { Message } from 'element-ui'
// axios.defaults.withCredentials=true;//让ajax携带cookie
// axios.defaults.headers.common['BdttOneyearAct'] = "b2814568b9e0b164132e77c400fffa2d";
// 自定义判断元素类型JS
function toType(obj) {
  return {}.toString
    .call(obj)
    .match(/\s([a-zA-Z]+)/)[1]
    .toLowerCase()
}
// 参数过滤函数
function filterNull(o) {
  for (var key in o) {
    if (o[key] === '' || o[key] === null) {
      delete o[key]
    }
    if (toType(o[key]) === 'string') {
      o[key] = o[key].trim()
    } else if (toType(o[key]) === 'object') {
      o[key] = filterNull(o[key])
    } else if (toType(o[key]) === 'array') {
      o[key] = filterNull(o[key])
    }
  }
  return o
}
/*
  接口处理函数
  这个函数每个项目都是不一样的，我现在调整的是适用于
  https://cnodejs.org/api/v1 的接口，如果是其他接口
  需要根据接口的参数进行调整。参考说明文档地址：
  https://cnodejs.org/topic/5378720ed6e2d16149fa16bd
  主要是，不同的接口的成功标识和失败提示是不一致的。
  另外，不同的项目的处理方法也是不一致的，这里出错就是简单的alert
*/

function getCookie(key) {
  var arr1 = document.cookie.split('; ')
  for (var i = 0; i < arr1.length; i++) {
    var arr2 = arr1[i].split('=')
    if (arr2[0] == key) {
      return decodeURI(arr2[1])
    }
  }
}
var heads = getCookie('ECSCP_ID')
if (typeof heads == 'undefined') {
  heads = ''
}
// console.log(heads)
// console.log(typeof(heads))
axios.defaults.timeout = 20000
function apiAxios(method, url, params, success, failure) {
  if (params) {
    params = filterNull(params)
  }
  let baseURL = null
  ROOT.map(o => {
    if (url.indexOf(o.key) == 0 || url.indexOf(o.key) == 1) {
      baseURL = o.val
    }
  })
  function change(url) {
    return encodeURIComponent(url) && url
  }
  axios({
    method: method,
    url: change(url),
    data: method === 'POST' || method === 'PUT' ? params : null,
    params: method === 'GET' || method === 'DELETE' ? params : null,
    baseURL: baseURL,
    withCredentials: true
    // headers: { 'X-HH-Authorization': 'b2814568b9e0b164132e77c400fffa3f' },// heads'b2814568b9e0b164132e77c400fffa2c'
  })
    .then(function(res) {
      if (res.data) {
        success(res.data)
      }
    })
    .catch(err => {
      if (failure) {
        failure(err)
      }
      console.log(err, '错误信息')
      Message.error({
        title: '错误',
        message: err.message
      })
      if (
        String(err)
          .toLowerCase()
          .indexOf('timeout') != -1
      ) {
        Message.error({
          title: '错误',
          message: '服务器繁忙，请稍后重试'
        })
        return
      }
    })
}

// 返回在vue模板中的调用接口
export default {
  get: function(url, params, success, failure) {
    return apiAxios('GET', url, params, success, failure)
  },
  post: function(url, params, success, failure) {
    return apiAxios('POST', url, params, success, failure)
  },
  put: function(url, params, success, failure) {
    return apiAxios('PUT', url, params, success, failure)
  },
  delete: function(url, params, success, failure) {
    return apiAxios('DELETE', url, params, success, failure)
  },
  axios
}
