import axios from 'axios'
import qs from 'qs'
import XXTEA from '../assets/js/xxtea'
import CryptoJS from 'crypto-js'
import utils from '../util/util'
import ROOT from '../api/root' //baseUrl配置
import { Message } from 'element-ui'

// import store from '../store/index'
import { ENUM } from '../const/enum'

const SIGN_KEY = 'arc4random()'
const ENCRYPT_KEY = 'getprogname()'

function getUserAgent() {
  let userAgent = ''
  let platform = 'Mozilla'
  const { width, height } = window.screen
  var lang = navigator.systemLanguage ? navigator.systemLanguage : navigator.language
  userAgent =
    'Platform/' + platform + ', Device/Unknown, Lang/' + lang + ', ScreenWidth/' + width + ', ScreenHeight/' + height
  return userAgent
}

// 超时时间
axios.defaults.timeout = 10e3
// 请求
axios.interceptors.request.use(
  config => {
    let isAPIRequest = config.url.indexOf(process.env.VUE_APP_APPSERVER_HOST) == 0 ? true : false
    if (isAPIRequest) {
      if (config.method === 'get') {
        config.withCredentials = true
      }

      let token = null
      if (window.token) {
        token = window.token
      }
      if (process.env.NODE_ENV === 'development') {
        // 商家管理token uid=42 塔城酒业
        const supplier_token =
          'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJ1aWQiOjQyLCJleHAiOjIwMDAwMDAwMDAsInBsYXRmb3JtIjoiIn0.Ckiic-2K6jh2FC00m2ox5saRJqPUpbPOO6Nt1FqLaRM'
        // 平台管理token uid=2
        const platform_token =
          'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJ1aWQiOjIsImV4cCI6Mjg0MDExMjAwMCwicGxhdGZvcm0iOiIifQ.x7sXrFHcDZUEiIkVq2JDO1FQxpOsXXsq0qTaAKuGQNw'
        // admin uid=2 的永久token
        if (config.isMerchant) {
          token = supplier_token
        } else {
          token = platform_token
        }
      }
      config.headers['X-ADMIN-Authorization'] = token
      config.headers['X-ADMIN-UserAgent'] = getUserAgent()
      config.headers['X-HH-Ver'] = process.env.VUE_APP_APPSERVER_VERSION

      if (config.method !== 'get') {
        config.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8'
        let post_body = qs.stringify(config.data)

        if (process.env.VUE_APP_ENCRYPTED) {
          // timestamp: 客户端秒级时间戳
          let timestamp = Date.parse(new Date()) / 1000 + ''
          // sign: HMAC-SHA256( timestamp + post_body, SIGN_KEY )
          let sign = CryptoJS.HmacSHA256(timestamp + post_body, SIGN_KEY)
          // xSign格式: sign,timestamp
          let xSign = sign + ',' + timestamp
          config.headers['X-ADMIN-Sign'] = xSign

          let encry_post_body = ''
          let body = null
          if (post_body && post_body.length) {
            encry_post_body = XXTEA.encryptToBase64(post_body, ENCRYPT_KEY)
            body = qs.stringify({ x: encry_post_body })
          }
          config.data = body
        } else {
          config.headers['X-HH-Sign'] = null
          config.data = post_body
        }
      }
    }
    return config
  },
  error => {
    return Promise.reject(error)
  }
)

// 响应
axios.interceptors.response.use(
  response => {
    if (response) {
      let isAPIRequest = response.config.url.indexOf(process.env.VUE_APP_APPSERVER_HOST) == 0 ? true : false
      let flag = false
      ROOT.forEach(function(value) {
        if (response.config.url.indexOf(value.val) != -1) {
          flag = true
        }
      })
      // java后台跳出拦截
      if (flag) {
        return response
      }
      if (isAPIRequest) {
        if (process.env.VUE_APP_ENCRYPTED) {
          // 解密
          if (response.data && response.data.data) {
            var raw = XXTEA.decryptFromBase64(response.data.data, ENCRYPT_KEY)
            var json = JSON.parse(raw)
            if (json) {
              delete response.data.data
              for (var key in json) {
                response.data[key] = json[key]
              }
            }
            logResponseParams(response)
            return response.data
          } else if (response.data && response) {
            let errorMessage = response.data.message
            let errorCode = response.data.code
            if (response.data.error) {
              logErrorInfo(response.config.url, errorCode, errorMessage)
              onAuthInvaild(errorCode, response.data)
              return Promise.reject({ errorCode: errorCode, errorMsg: errorMessage })
            }
          }
        } else {
          if (response) {
            if (response.data && (response.data.code === ENUM.ERROR_CODE.OK || !response.data.code)) {
              logResponseParams(response)
              if (response.config.responseType == 'blob') {
                let filename = decodeURI(response.headers['content-disposition']).match(
                  /[='"]([^.='"]*\.[^.='"]{1,4})"?$/
                )
                if (!filename) {
                  return Promise.reject({ errorCode: 100, errorMsg: '下载失败' })
                }
                response.filename = decodeURIComponent(filename[1])
                return response
              } else {
                if (response.data.info) {
                  Message({ message: response.data.info, type: 'warning' })
                }
                return response.data.data
              }
            } else {
              let errorMessage = response.data.info
              let errorCode = response.data.code
              logErrorInfo(response.config.url, errorCode, errorMessage)
              onAuthInvaild(errorCode, response.data)
              return Promise.reject({ errorCode: errorCode, errorMsg: errorMessage })
            }
          }
        }
      } else {
        console.log('请求地址错误!')
      }
    } else {
      console.log('网络错误')
    }
  },
  error => {
    return Promise.reject(error.response ? error.response.data : error.response)
  }
)

function logResponseParams(response) {
  if (process.env.NODE_ENV === 'development') {
    let str = JSON.stringify(response.data)
    console.groupCollapsed(
      '%c' + utils.formatDate('HH:mm:ss.SSS') + '%c请求' + response.config.url + '\n',
      'font-weight:normal;color:#fff;background:#35495e;padding:2px 7px;border-radius:3px 0 0 3px;',
      'font-weight:normal;color:#fff;background:#41b883;padding:2px 7px;border-radius:0 3px 3px 0;',
      str.length < 80 ? str : str.substring(0, 80) + '...'
    )
    console.count('SN')
    response.config.data && console.log('%c参数\n', 'color:#aaa', response.config.data)
    console.log('%c响应\n', 'color:#aaa', response.data)
    console.groupEnd()
  }
}

function logErrorInfo(url, errorCode, errorMessage) {
  if (process.env.NODE_ENV === 'development') {
    console.log('request url is: ', url)
    console.log('网络错误, 错误代码:=' + errorCode + '错误信息:=' + errorMessage)
  }
}

function onAuthInvaild(errorCode) {
  if (errorCode == ENUM.ERROR_CODE.TOKEN_INVALID) {
    // TOKEN失效就刷新页面
    setTimeout(() => {
      location.reload()
    }, 2e3)
  }
}

// 发起请求(返回结果没有错误时，会将data内的数据提出来并返回)
export function fetchEndpointBlob(reqUrl, type = 'POST', data = {}) {
  return fetchEndpoint(reqUrl, type, data, 1)
}

export function fetchEndpointMerchant(reqUrl, type = 'POST', data = {}) {
  return fetchEndpoint(reqUrl, type, data, 0, true)
}

// 发起请求
export function fetchEndpoint(reqUrl, type = 'POST', data = {}, api_type = 0, isMerchant = false) {
  if (!reqUrl) {
    return
  }
  type = type.toUpperCase()
  if (api_type != 3) {
    reqUrl = process.env.VUE_APP_APPSERVER_HOST + reqUrl
  }

  if (type == 'GET') {
    let dataStr = '' //数据拼接字符串
    Object.keys(data).forEach(key => {
      dataStr += key + '=' + data[key] + '&'
    })

    if (dataStr !== '') {
      dataStr = dataStr.substr(0, dataStr.lastIndexOf('&'))
      reqUrl = reqUrl + '?' + dataStr
    }
  }

  let config = {
    method: type,
    url: reqUrl,
    data: data,
    isMerchant
  }
  if (api_type == 1) {
    config.responseType = 'blob'
    config.timeout = 200e3
  }

  return new Promise((resolve, reject) => {
    axios(config).then(
      res => {
        if (Object.prototype.toString.call(res) === '[object Object]') {
          // 若可以把对象转回简单的类型就转
          let keys = Object.keys(res)
          if (keys.length == 1 && 'scalar' in res) {
            // 后端返回非数组、对象的类型时，会自动转为对象并给予键值 scalar，影响的类型有: 布林、数字、字符串
            res = res['scalar']
          } else if (
            Object.keys(res).every(function(value, index) {
              return value == index
            })
          ) {
            // 后端返回非数组类型时，会自动转为对象
            res = Object.values(res)
          }
        }
        resolve(res)
      },
      error => {
        error.errorMsg && Message({ message: error.errorMsg, type: 'warning' })
        reject(error)
      }
    )
  })
}
